/* Variables */

:root {
  --color-white: #FFFFFF;
  --color-black: #000;
  --color-light-gray: #5A5F74;
  --color-dark: #241B2A;
  --color-dark-violet: #280047;
  --color-bg-primary: #1D1324;
  --color-accent-gradient: linear-gradient(90deg, #332694 0%, #642895 63.02%);
  --color-green-light: #1EBC7F;
  --color-invalid: #e61d1d;
  --color-yellow: #EEFF00;
  --font-raleway: "Raleway", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* Base */

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

img {
  max-width: 100%;
}

/* Animation */

/* Burger-menu */

@-webkit-keyframes rotate-burger-before {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes rotate-burger-before {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@-webkit-keyframes rotate-burger-after {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-45deg);
  }
}

@keyframes rotate-burger-after {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-45deg);
  }
}

/* Rotate */

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* Base Classes */

/* Containers */

.container {
  max-width: 1270px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container-full {
  padding-left: 15px;
