/* Page */

.page {
  margin: 0;
  font-family: var(--font-raleway);
  font-size: 16px;
  line-height: 1.5;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.page__container {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  flex: 1 0 auto;
}

/* Hystmodal*/

.hystmodal__opened {
  position: fixed;
  right: 0;
  left: 0;
  overflow: hidden;
}

.hystmodal--active {
  opacity: 1;
}

.hystmodal--moved,
.hystmodal--active {
  pointer-events: auto;
  visibility: visible;
}

.hystmodal__wrap {
  flex-shrink: 0;
  flex-grow: 0;
  width: 100%;
  min-height: 100%;
  margin: auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
}

.hystmodal__window {
  margin: 50px 0;
  box-sizing: border-box;
  flex-shrink: 0;
  flex-grow: 0;
  overflow: visible;
  transition: opacity 0.2s ease 0.3s;
  opacity: 0;
}

.hystmodal--active .hystmodal__window {
  opacity: 1;
}

/* Modal*/

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  background-color: transparent;
  z-index: 999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: blur(50px);
          backdrop-filter: blur(50px);
  visibility: hidden;
  overflow: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
  transition-delay: 0.3s;
  position: fixed;
}

.modal.hystmodal--active {
  visibility: visible;
  opacity: 1;
  position: fixed;
  transition-delay: 0s;
}

.modal__body {
  position: relative;
  display: flex;
  flex-direction: column;
  background: var(--color-white);
  max-width: 555px;
  width: 100%;
  padding: 40px 35px 28px;
  overflow-x: hidden;
  border-radius: 10px;
  margin-top: 90px;
}

.modal__close {
  display: inline-block;
  position: absolute;
  right: 15px;
  top: 15px;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.modal__close-svg {
  width: 30px;
  height: 30px;
  fill: var(--color-black);
  stroke: var(--color-black);
}

.modal__title {
  margin-bottom: 15px;
}

.modal__form {
  font-family: var(--font-raleway);
  width: 100%;
  max-width: 555px;
  margin: 0 auto;
}

.modal__field ~ .modal__field {
  margin-top: 30px;
}

.modal__field-text {
  margin-bottom: 25px;
}

.modal__input {
  position: relative;
  font-size: 16px;
  font-weight: 300;
  line-height: 17px;
  width: 100%;
  padding: 15px;
  background: rgba(141, 141, 141, 0.1);
  border: 1px solid rgba(18, 18, 18, 0.1);
  color: var(--color-dark);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.04);
  border-radius: 10px;
}

.modal__input:focus,
.modal__input:target,
.modal__input:active {
  outline: 1px solid var(--color-dark-violet);
  border: 1px solid var(--color-dark-violet);
}

.modal__input::-webkit-input-placeholder {
  color: var(--color-light-gray);
}

.modal__input::-moz-placeholder {
  color: var(--color-light-gray);
}

.modal__input:-ms-input-placeholder {
  color: var(--color-light-gray);
}

.modal__input::placeholder {
  color: var(--color-light-gray);
}

.modal__input--textarea {
  max-height: 150px;
}

.modal__actions {
  position: relative;
  margin-top: 25px;
  text-align: center;
}

.modal__btn.button--secondary {
  padding: 15px 64px;
}

.modal__btn.button--secondary:hover {
  color: var(--color-dark-violet);
}

.modal__btn.button--secondary:target {
  color: var(--color-white);
}

.modal__btn.button--secondary:focus {
  color: var(--color-white);
}

.modal__btn.button--secondary:active {
  color: var(--color-dark-violet);
}

.modal__actions .wpcf7-spinner {
  position: absolute;
  top: 50%;
  background-color: transparent;
  transform: translateY(-50%);
  opacity: 1;
  width: 20px;
  height: 20px;
}

.modal__actions .wpcf7-spinner:before {
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: transparent;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  border: 3px solid var(--color-dark-violet);
  border-top: 3px solid #595959;
  transform-origin: center;
  -webkit-animation: rotate 1s infinite linear backwards;
          animation: rotate 1s infinite linear backwards;
}

.modal .wpcf7 form .wpcf7-response-output {
  font-family: var(--font-raleway);
  font-size: 14px;
  text-align: center;
  margin: 20px 0 0;
  padding: 5px 1em;
  border: 2px solid var(--color-yellow);
  color: var(--color-black);
  border-radius: 10px;
}

.modal .wpcf7 form.invalid .wpcf7-response-output,
.modal .wpcf7 form.unaccepted .wpcf7-response-output {
  border: 1px solid var(--color-yellow);
}

.modal .wpcf7 form.sent .wpcf7-response-output {
  border-color: var(--color--green-light);
}

.modal__form .wpcf7-not-valid-tip {
  text-align: center;
  font-family: var(--font-raleway);
  font-size: 12px;
  margin-top: 5px;
  color: var(--color-invalid);
  position: absolute;
  width: 100%;
  bottom: -37px;
  left: 50%;
  transform: translateX(-50%);
}

.modal__agree-text {
  margin-top: 18px;
}

.modal__agree-text .wpcf7-acceptance .wpcf7-list-item {
  font-family: var(--font-raleway);
  font-size: 12px;
  font-weight: 400;
  color: var(--color-black);
  text-decoration: none;
  display: flex;
  justify-content: center;
}

.modal__agree-text .wpcf7-acceptance .wpcf7-list-item:hover {
  text-decoration: underline;
}

.modal__agree-text .wpcf7-acceptance label {
  position: relative;
  display: flex;
  align-items: center;
}

.modal__checkbox--agree {
  margin-right: 25px;
  position: relative;
  z-index: -1;
  opacity: 0;
}

.modal__agree-text .wpcf7-acceptance label > span::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  z-index: 1;
  opacity: 1;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 80%;
  width: 20px;
  height: 20px;
  border: 2px solid #000;
  border-radius: 5px;
}

.modal__agree-text .modal__agree-text .wpcf7-acceptance label > span:hover::before {
  border: 2px solid #000;
}

.modal__agree-text .wpcf7-acceptance label > .modal__checkbox--agree:focus + span::before {
  border: 2px solid #000;
}

.modal__agree-text .wpcf7-acceptance label > .modal__checkbox--agree:focus:not(:checked) + span::before {
  border: 2px solid #000;
}

.modal__agree-text .wpcf7-acceptance label > .modal__checkbox--agree:disabled + span::before {
  border: 2px solid #000;
}

.modal__agree-text .wpcf7-acceptance label > .modal__checkbox--agree:checked + span::before {
  border: 2px solid #000;
  background-image: url("data:image/svg+xml;base64,");
}

/* Header */

.header {
  position: absolute;
  width: 100%;
  z-index: 10;
}

.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0 35px;
  border-bottom: 1px solid var(--color-white);
}

.header__inner {
  display: flex;
  align-items: center;
}

.header__action {
  display: inline-flex;
  margin-left: 40px;
}

/* Logo */

.logo__img {
  display: block;
}

/* Menu */

.menu {
  font-family: var(--font-raleway);
  font-size: 16px;
  font-weight: normal;
}

.menu__list {
  margin: 0;
  padding: 0;
}

.header__menu-list.menu__list {
  display: flex;
}

.menu__item {
  position: relative;
  list-style: none;
}

.header__menu-item.menu__item ~ .header__menu-item.menu__item {
  margin-left: 40px;
}

.menu__link {
  color: var(--color-white);
  text-decoration: none;
  transition: border-bottom ease-in-out 0.15s;
  border-bottom: 1px solid transparent;
}

.menu__link:hover {
  border-bottom: 1px solid var(--color-white);
}

/* Buttons */

.button {
  font-family: var(--font-raleway);
  font-weight: 600;
  position: relative;
  cursor: pointer;
  display: inline-block;
  font-style: normal;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  line-height: 16px;
  border-radius: 50px;
  padding: 20px 40px;
  z-index: 10;
  box-shadow: 10px 10px 25px -10px rgba(0, 0, 0, 0.15);
  outline: var(--color-dark-violet);
  transition: background 0.25s ease-in-out, color 0.25s ease-in-out, border 0.25s ease-in-out;
  overflow: hidden;
}

/* Button primary */

.button--primary {
  border: 2px solid var(--color-dark-violet);
  color: var(--color-black);
  transition: color 0.3s, border 0.3s, background 0.3s;
}

.button--primary::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: var(--color-white);
  z-index: -1;
  transition: width 0.25s;
}

.button--primary:hover::before,
.button--primary:focus::before,
.button--primary:target::before {
  width: 0%;
}

.button--primary:hover,
.button--primary:focus,
.button--primary:target {
  color: var(--color-white);
  background: var(--color-accent-gradient);
  border: 2px solid var(--color-dark-violet);
}

.button--primary:active {
  color: var(--color-white);
  background: var(--color-accent-gradient);
}

.button--primary:disabled {
  opacity: 0.45;
}

/* Button secondary */

.button--secondary {
  border: 2px solid var(--color-dark-violet);
  color: var(--color-white);
  background: var(--color-accent-gradient);
  border: 2px solid var(--color-dark-violet);
  transition: color 0.3s, border 0.3s, background 0.3s;
}

.button--secondary::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 0%;
  top: 0;
  right: 0;
  background: var(--color-white);
  z-index: -1;
  transition: width 0.15s;
}

.button--secondary:hover::before,
.button--secondary:focus::before,
.button--secondary:target::before {
  width: 100%;
}

.button--secondary:hover {
  color: var(--color-dark-violet);
  background: var(--color-white);
  border: 2px solid var(--color-dark-violet);
}

.button--secondary:focus,
.button--secondary:target {
  color: var(--color-dark-violet);
  border: 2px solid var(--color-dark-violet);
}

.button--secondary:active::before {
  background: var(--color-accent-secondary);
}

.button--secondary:active {
  color: var(--color-dark-violet);
  border: 2px solid var(--color-dark-violet);
  background: var(--color-accent-secondary);
}

.button--secondary:disabled {
  opacity: 0.45;
}

/* Button small */

.button--small {
  border: 1px solid var(--color-dark-violet);
  padding: 11px 24px;
}

.button--small:hover {
  border: 1px solid var(--color-white);
}

.button--small:hover,
.button--small:focus,
.button--small:target {
  border: 1px solid var(--color-dark-violet);
}

/* Main-screen */

.main-screen {
  background: url("../img/main-screen-bg.png") no-repeat center;
  background-size: cover;
  padding: 300px 0 200px;
}

.main-screen__heading {
  max-width: 810px;
}

.main-screen__descr {
  margin-top: 40px;
}

/* Why-us */

.why-us {
  background: url("../img/why-us-decor.png") no-repeat;
  background-position: right bottom;
  background-size: contain;
}

.why-us__inner {
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(440px, auto));
  grid-gap: 65px;
  align-items: stretch;
  -ms-grid-rows: 1fr;
  grid-template-rows: 1fr;
}

.why-us__title {
  margin-bottom: 40px;
}

.why-us__features {
  position: relative;
  padding: 71px 55px 63px;
  border-radius: 20px;
}

.why-us__features-decor {
  position: absolute;
  top: -40px;
  right: 30px;
}

.why-us__features-svg {
  display: block;
}

.why-us__point {
  display: inline-flex;
  align-items: center;
}

.why-us__point ~ .why-us__point {
  margin-top: 40px;
}

.why-us__descr-point {
  margin-left: 25px;
}

.why-us__logos {
  margin-top: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.why-us__logo {
  display: block;
}

.why-us__logo ~ .why-us__logo {
  margin-left: 6.5em;
}

/* Products */

.products {
  background: url("../img/products-bg.jpg") no-repeat center;
  background-size: cover;
}

.products__grid {
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 0.33fr));
  grid-gap: 40px;
  justify-content: center;
  align-items: stretch;
  -ms-grid-rows: 1fr;
  grid-template-rows: 1fr;
}

.products__item {
  padding: 40px;
  border-radius: 20px;
  min-height: 450px;
  height: 100%;
}

.products__title {
  margin-top: 40px;
}

.products__descr {
  margin-top: 20px;
}

/* Documentation */

.documentation__item {
  padding: 40px 20px;
  border: 2px solid var(--color-dark-violet);
  border-radius: 20px;
}

.documentation__item ~ .documentation__item {
  margin-top: 60px;
}

.documentation__title {
  margin-bottom: 20px;
}

.documentation__descr + .documentation__title {
  margin-top: 30px;
}

.documentation__files {
  display: block;
  text-decoration: none;
}

.documentation__files:hover {
  text-decoration: underline;
}

.documentation__files ~ .documentation__files {
  margin-top: 20px;
}

.documentation__info {
  margin-top: 20px;
}

/* Feedback */

.feedback__descr {
  margin-top: 20px;
}

.feedback__actions {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.feedback__button ~ .feedback__button {
  margin-left: 20px;
}

/* Footer */

.footer {
  padding: 120px 0 40px;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--color-white);
  padding-bottom: 40px;
}

.footer__mail,
.footer__tel {
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-bottom ease-in-out 0.15s;
}

.footer__mail:hover,
.footer__tel:hover {
  border-bottom: 1px solid var(--color-white);
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0 0;
}

.footer__recomendation {
  display: flex;
  align-items: center;
}

.footer__text-recomendation {
  margin-left: 15px;
}

/* MEDIA QUERIES */

@media screen and (max-width: 1024px) {
  .hide-tablet {
    display: none !important;
  }

  .why-us__title {
    margin-bottom: 20px;
  }

  .why-us__logos {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .why-us__logos::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .products__grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, auto));
  }
}

@media screen and (max-width: 992px) {
  .hide-tabletSM {
    display: none !important;
  }
}

@media screen and (max-width: 768px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .container-full {
    padding-left: 15px;
    padding-right: 15px;
  }

  .hide-mobile {
    display: none !important;
  }

  .title--indent {
    margin: 0 0 20px;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .container-full {
    padding-left: 10px;
    padding-right: 10px;
  }

  .modal__body {
    padding: 16px 15px 30px;
    max-width: 100%;
  }

  .modal__actions .wpcf7-spinner {
    margin: 0 10px;
  }

  .why-us__inner {
    grid-template-columns: repeat(auto-fit, minmax(320px, auto));
  }

  .why-us__features {
    padding: 80px 31px 60px;
  }

  .feedback__actions {
    flex-direction: column;
  }

  .feedback__button ~ .feedback__button {
    margin-left: 0;
    margin-top: 20px;
  }

  .footer__inner {
    flex-direction: column;
  }

  .footer__mail,
  .footer__tel {
    margin-top: 20px;
  }

  .footer__bottom {
    flex-direction: column;
  }

  .footer__recomendation {
    margin-top: 20px;
  }
}

@media screen and (max-width: 400px) {
  .modal__actions .wpcf7-spinner {
    margin: 0 4px;
  }
}